import { createWebHistory, createRouter } from "vue-router";

//Component 파일을 여기서 import 해야 router으로 사용 가능합니다

import Register from '../components/Register.vue'
import Login from '../components/Login.vue'
import Project from '../components/Project.vue'
import Dashboard from '../components/Dashboard.vue'
import Upload from '../components/Upload.vue'



const routes = [{
        path: '/',
        component: Project, //(import해온 컴포넌트)
    },
    {

        path: '/register',
        component: Register, //(import해온 컴포넌트)
    },

    {
        path: '/login',
        component: Login, //(import해온 컴포넌트)
    },
    {
        path: '/dashboard',
        component: Dashboard, //(import해온 컴포넌트)
    },
    {
        path: '/upload',
        component: Upload, //(import해온 컴포넌트)
    },

];


const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;